font-family: 'Orbitron', sans-serif;}
.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  color: white;
  flex-grow: 1;
    /* Allows navigation to take available space and be centered */
    text-align: center;
  /* Adjust based on your design */
}

nav ul {
  list-style: none;
  display: flex;
  padding: 0;
}

.nav-link {
  margin: 0 20px;
  position: relative;
}

.nav-link a {
  text-decoration: none;
  color: inherit;
}

.nav-link h1 {
  margin: 0;
  font-size: 18px;
  /* Adjust based on your design */
  cursor: pointer;
  position: relative;
  transition: color 0.3s ease-in-out;
}

/* Add a digital-style underline animation on hover */
.nav-link::before {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #b80ad3;
  /* Animation color */
  transform: scaleX(0);
  transform-origin: bottom left;
  transition: transform 0.3s ease-in-out;
}

.nav-link:hover::before {
  transform: scaleX(1);
  transform-origin: bottom left;
}

/* Example: Add a digital-style glow effect on hover */
.nav-link:hover h1 {
  color: #750675;
  /* Change color on hover */
  text-shadow: 0 0 10px #e529f6;
  /* Glow effect */
}
