.chat-container {
  background: #750675;
  color: #fff;
  padding: 20px;
  margin: 30px;
  border-radius: 10px;
  
}

.chat-messages {
  overflow-y: auto;
  max-height: 300px;
}

.message {
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
}

.message.user {
  background: #430357;
}

.message.assistant {
  background: #4D067F;
}

.chat-input-form {
  margin-top: 15px;
  display: flex;
}

.chat-input-form input {
  flex: 1;
  padding: 10px;
  border-radius: 5px;
  border: none;
  margin-right: 10px;
  font-size: 14px;
}

.chat-input-form button {
  background: #4D067F;
  color: #fff;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}