/* ContactUs.css */

.contact-us-container {
  background: #750675;
  color: #fff;
  padding: 50px;
  border-radius: 10px;
  margin: 30px;
  max-width: 400px;
  /* Limit the width for a more compact appearance */
  margin: 0 auto;
  /* Center the container */
}

h2 {
  margin-bottom: 20px;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

label {
  margin: 10px 0;
}

input,
textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: none;
  border-radius: 5px;
  

  font-size: 14px;
}

button {
  background: #4D067F;
  color: #fff;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
}