/* attraction.css */

.attraction-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.attraction-title {
  font-size: 24px;
  color: #750675;
  margin-bottom: 20px;
}

.attraction-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.attraction-card {
  background-color: #f5f5f5;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.attraction-name {
  font-size: 18px;
  color: #750675;
  margin-bottom: 10px;
}

.attraction-description {
  font-size: 14px;
  margin-bottom: 10px;
  color: #750675;
}

.attraction-rating,
.attraction-price {
  font-size: 14px;
  margin-bottom: 5px;
  color: #750675;
}

.attraction-image {
  width: 100%;
  height: auto;
  border-radius: 4px;
  margin-top: 10px;
}

.no-attractions-message {
  color: #ff0000;
  font-weight: bold;
}