


/* flightsearch.css */

.flight-search-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 60px;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.flight-search-title {
  font-size: 24px;
  color: #750675;
  margin-bottom: 20px;
}

.flight-inputs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.flight-input {
  margin-bottom: 20px;
  
}

.input-label {
  display: block;
  font-size: 16px;
  color: #750675;
  margin-bottom: 5px;
}

.input-text {
  color:black;
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.search-button {
  background-color: #750675;
  color: #fff;
  padding: 10px 15px;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.flight-results {
  margin-top: 20px;
}

.results-title {
  font-size: 20px;
  color: #750675;
  margin-bottom: 10px;
}

.flight-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.flight-card {
  background-color: #f5f5f5;
  padding: 10px;
  border-radius: 4px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.no-flights-message {
  color: #ff0000;
  font-weight: bold;
}